.animated {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}
.tolong-in {
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
  -webkit-animation-name: tolong-in;
          animation-name: tolong-in;
}
.clear:after {
  content: '';
  display: block;
  clear: both;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.btn {
  display: inline-block;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 0 4px;
  text-align: center;
  cursor: pointer;
  -webkit-transition: box-shadow 0.2s;
  transition: box-shadow 0.2s;
}
.btn[disabled],
.btn.disabled {
  cursor: not-allowed;
  opacity: 0.6;
}
.btn:active,
.btn:focus,
.btn:active:focus {
  outline: 0;
}
.btn.grande {
  border-width: 2px;
  width: 100px;
  font-size: 14px;
  line-height: 36px;
}
.btn.normal {
  width: 106px;
  font-size: 13px;
  line-height: 34px;
}
.btn.tall {
  border-width: 1px;
  width: 78px;
  font-size: 14px;
  line-height: 28px;
}
.btn.small {
  border-width: 1px;
  width: 96px;
  font-size: 13px;
  line-height: 26px;
}
.btn.mini {
  border-width: 1px;
  width: 72px;
  font-size: 13px;
  line-height: 24px;
}
.btn.micro {
  border-width: 1px;
  width: 50px;
  font-size: 12px;
  line-height: 20px;
}
.btn.gray {
  border: 1px solid #dee1e4;
  box-shadow: 0 3px 4px 0 rgba(141,155,165,0.1);
  color: #333;
  background: #fff;
}
.btn.gray:hover {
  box-shadow: 0 5px 8px 0 rgba(141,155,165,0.2);
}
.btn.default {
  border: 1px solid #4dbf9f;
  box-shadow: 0 3px 4px 0 rgba(16,132,160,0.24);
  color: #4dbf9f;
  background: #fff;
}
.btn.default:hover {
  box-shadow: 0 5px 8px 0 rgba(16,132,160,0.33);
}
.btn.light {
  border-color: #dddbe6;
  color: #4dbf9f;
  background: #fff;
}
.btn.light:hover {
  color: #1f1e26;
  background: #dddbe6;
}
.btn.primary {
  box-shadow: 0 3px 4px 0 rgba(16,132,160,0.24);
  border-color: #4dbf9f;
  color: #fff;
  background: #4dbf9f;
}
.btn.primary:hover {
  box-shadow: 0 5px 8px 0 rgba(16,132,160,0.33);
}
.btn.grande.primary {
  box-shadow: 0 11px 15px 1px rgba(16,132,160,0.33);
}
.btn.file-btn {
  position: relative;
  overflow: hidden;
}
.btn.file-btn .file-input {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  opacity: 0;
  font-size: 40px;
}
.btn-group.grande .btn {
  margin: 0 8px;
}
.btn-group.normal .btn {
  margin: 0 6px;
}
.btn-group .btn:first-child {
  margin-left: 0;
}
.btn-group .btn:last-child {
  margin-right: 0;
}
.btn-group.pick {
  display: inline-block;
  font-size: 0;
}
.btn-group.pick .btn {
  margin: 0;
  border-radius: 0;
}
.btn-group.pick .btn:first-child {
  border-radius: 4px 0 0 4px;
}
.btn-group.pick .btn:last-child {
  border-radius: 0 4px 4px 0;
}
.link {
  color: #3c95ef;
}
.link.primary,
.text.primary,
.icon.primary {
  color: #4dbf9f;
}
.link.warning,
.text.warning,
.icon.warning {
  color: #ff9a2d;
}
.link.success,
.text.success,
.icon.success {
  color: #03daaf;
}
/* layout */
#root {
  display: flex;
  position: relative;
  width: 100%;
  min-height: 100%;
  padding-top: 64px;
  flex-direction: column;
}
footer.public-footer {
  padding: 4px 0 12px;
  text-align: center;
  color: #92a0bf;
  font-size: 12px;
}
footer.public-footer a.link {
  margin: 0 4px;
  text-decoration: underline;
}
/* header */
header.public-header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  padding: 0 40px;
  box-shadow: 0 1px 4px 0 rgba(14,130,220,0.15);
  width: 100%;
  height: 64px;
  vertical-align: middle;
  line-height: 64px;
  background: -webkit-gradient(linear, left top, right top, from(#4d5f94), to(#554d94));
  background: linear-gradient(to right, #4d5f94, #554d94);
}
header.public-header a.logo {
  display: inline-block;
  margin-right: 30px;
  width: 90px;
  height: 100%;
  vertical-align: middle;
  line-height: 64px;
}
header.public-header a.logo svg {
  vertical-align: middle;
}
header.public-header a.plaza,
header.public-header nav a.create,
header.public-header nav .message a,
header.public-header nav a.setting,
header.public-header nav a.fav {
  display: inline-block;
  font-size: 16px;
  vertical-align: middle;
  line-height: 20px;
}
header.public-header a.plaza > i.icon-font,
header.public-header nav a.create > i.icon-font,
header.public-header nav .message a > i.icon-font,
header.public-header nav a.setting > i.icon-font,
header.public-header nav a.fav > i.icon-font {
  vertical-align: top;
  font-size: 16px;
  line-height: 20px;
}
