a.attention-card {
  display: block;
  position: relative;
  margin: 0 auto;
  border-radius: 4px;
  padding: 10px 16px 12px;
  min-width: 360px;
  height: 168px;
  box-shadow: 0 8px 18px 0 rgba(41,104,152,0.26);
  text-align: left;
  color: #333;
  background: #fff;
}
a.attention-card:focus {
  text-decoration: none;
}
a.attention-card:hover {
  color: #333;
}
a.attention-card .title {
  position: relative;
  margin-bottom: 6px;
  padding-right: 64px;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
a.attention-card .title i,
a.attention-card .title h4 {
  font-size: 18px;
  line-height: 24px;
  vertical-align: top;
}
a.attention-card .title i.icon-font {
  color: #5b6d95;
}
a.attention-card .title h4 {
  display: inline-block;
  font-weight: 600;
}
a.attention-card p.update {
  margin-bottom: 8px;
  color: #92a0bf;
  font-size: 12px;
}
a.attention-card p.summary {
  margin-bottom: 0;
  height: 64px;
  overflow: hidden;
}
