main.home {
  height: calc(100vh - 64px);
  overflow: auto;
}
main.home .container-wrapper {
  min-height: calc(100% - 34px);
}
main.home .cards {
  position: relative;
  margin: 0 auto;
  padding: 20px 20px 0;
}
main.home .cards > ul {
  margin: 0 -10px;
}
main.home .cards > ul:after {
  content: '';
  display: block;
  clear: both;
}
main.home .cards > ul > li {
  float: left;
  margin-bottom: 20px;
  padding: 0 10px;
  text-align: center;
  -webkit-animation-name: slideInUp;
          animation-name: slideInUp;
}
main.home .cards > ul > li .card {
  width: 80%;
