  margin: 20px auto 72px;
  padding: 0 20px;
  width: 1000px;
  max-width: 100%;
  line-height: 1.6;
}
main.article .container-wrapper h1,
main.article .container-wrapper h2,
main.article .container-wrapper h3,
main.article .container-wrapper h4,
main.article .container-wrapper h5,
main.article .container-wrapper h6 {
  margin-bottom: 12px;
